type InitialJACStateType = typeof initialState

const JACReducer = function (state: InitialJACStateType = initialState, action: AllACJACTypes): InitialJACStateType {
    let stateCopy: InitialJACStateType

    switch (action.type) {
        default: return state
    }
}

type JACReducerType = typeof JACReducer


type GetActionWInferType<T> = T extends { [key: string]: infer U } ? U : never

type AllACJACTypes = ReturnType<GetActionWInferType<typeof JACReducerActionCreators>>

const JACReducerActionCreators = {
    fakeAC: () => ({ type: 'JACData/FAKE_AC' } as const),
}

export { JACReducer }
export type { JACReducerType, JACItemType, InitialJACStateType }